import React from "react";
import { Box } from "../../../../ui/controls";
import { ContainerSize, TextColor } from "../../../../ui/types";
import { BackgroundColor } from "../../../../ui/types/background-color.enum";
import largeHalfCircle from "../../../../images/large-half-circle.svg";
import { useNavigate } from "react-router-dom";
import {
  StyledBox,
  StyledBoxContainer,
  StyledButtonWithSemicircle,
  StyledContainer,
  StyledGreenPuzzle,
  StyledImg,
  StyledInfoSection,
  StyledPuzzles,
  StyledTestBox,
  StyledTestSection,
  StyledText1,
  StyledText1Container,
  StyledSubtitle3,
  Actions,
} from "./start-styles";
import { AppContext } from "../../../../components/contexts";
import { useContext, useEffect } from "react";
import { useLazyQuery } from "@apollo/client";
import { NewTest } from "../../../../types/newTest";
import { toast } from "react-hot-toast";
import { newTestQuery, setTestId, testBaseUrl } from "../../../../utils";

interface StartPageProps {
  forClosePerson: boolean;
  title: string;
}

const StartPage: React.FC<StartPageProps> = ({ forClosePerson, title }) => {
  const [getTestId, {}] = useLazyQuery<NewTest>(newTestQuery(forClosePerson), {
    fetchPolicy: "cache-and-network",
    nextFetchPolicy: "network-only",
  });
  const { setLastQuestionId } = useContext(AppContext);
  const navigate = useNavigate();
  const routeForTest = testBaseUrl(forClosePerson);

  const onStartBtnClick = () => {
    getTestId()
      .then((res) => {
        setTestId(JSON.stringify(res.data?.newTest), forClosePerson);
        if (setLastQuestionId) {
          setLastQuestionId(`description`);
          navigate(`${routeForTest}/description`);
        }
      })
      .catch(() => toast.error(`   `, { id: "error" }));
  };

  useEffect(() => {
    if (setLastQuestionId) {
      setLastQuestionId(`start`);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <Box mb={5}>
      <StyledInfoSection flex>
        <StyledContainer
          size={ContainerSize.Medium}
          bgColor={BackgroundColor.Alt3}
        >
          <StyledBoxContainer pt={3} pr={18} pb={3} pl={3}>
            <StyledText1Container mb={3} textColor={TextColor.Primary}>
                    
               . &nbsp;    
                     .
            </StyledText1Container>
            <StyledText1Container mb={3} textColor={TextColor.Primary}>
                     ,
                     
                   
               .
            </StyledText1Container>
            <StyledText1Container textColor={TextColor.Primary}>
                   
              ,        
               .
            </StyledText1Container>
          </StyledBoxContainer>
          <StyledImg src={largeHalfCircle} />
        </StyledContainer>
      </StyledInfoSection>

      <StyledTestSection flex centered borderBox pt={8}>
        <StyledTestBox flex column>
          <StyledSubtitle3 maxWidth={749} mb={7} textColor={TextColor.Primary}>
            {title}
          </StyledSubtitle3>
          <StyledBox flex>
            <StyledPuzzles />
            <StyledTestBox flex column>
              <Actions>
                <StyledButtonWithSemicircle
                  maxWidth={350}
                  buttonText=" "
                  onClick={onStartBtnClick}
                />
              </Actions>
              <StyledText1 maxWidth={500} mt={7}>
                      
                  ,     
                .
              </StyledText1>
            </StyledTestBox>
            <StyledGreenPuzzle animated={false} />
          </StyledBox>
        </StyledTestBox>
      </StyledTestSection>
    </Box>
  );
};

export default StartPage;
